  color: #FFFFFF;
  background-color: #6EBBD3;
  font-family: "montserrat-bold", Arial, Helvetica, sans-serif;
  font-size: 14px;
  line-height: 17px;
  cursor: pointer;
  flex-grow: 1;
  height: 65px;
  border-radius: 10px;
  padding: 10px 10px 10px 10px;
}
.subscribe__btn:hover {
