  }
}

.checkbox {
  display: flex;
  position: relative;
}

.checkbox__label {
  display: block;
  cursor: pointer;
  position: relative;
  text-align: center;
  font-size: 14px;
  line-height: 17px;
  color: #FFFFFF;
  font-family: "montserrat-regular", Arial, Helvetica, sans-serif;
  text-transform: initial;
  padding-left: 37px;
}
.checkbox__label:hover {
  opacity: 0.9;
}
.checkbox__label::before {
  position: absolute;
  display: block;
  content: "";
  width: 18px;
  height: 18px;
  top: 0;
  left: 0;
  background: #6EBBD3;
  border-radius: 3px;
}
.checkbox__label::after {
  position: absolute;
  content: "";
  width: 10px;
  height: 7px;
  left: 4px;
  top: 5px;
  z-index: 3;
  background: url(../images/checkbox.svg) no-repeat center;
  opacity: 0;
}

.checkbox__input {
  position: absolute;
  opacity: 0;
  z-index: 1;
  top: 0;
  left: 0;
  width: 18px;
  height: 18px;
}

.checkbox__input:checked + .checkbox__label::after {
  opacity: 1;
}

.scrollup {
  padding: 0;
  border: 0;
  font: inherit;
  color: inherit;
  background: #6EBBD3 url(../images/arrow-up.svg) no-repeat center;
  cursor: pointer;
  position: absolute;
  display: block;
  width: 59px;
  height: 59px;
  bottom: -32px;
  right: 30px;
}
.scrollup:hover {
  background-color: rgba(110, 187, 211, 0.8);
}
.scrollup:active {
  background-color: rgba(110, 187, 211, 0.5);
}
@media only screen and (max-width: 767px) {
  .scrollup {
    display: none;
  }
}

.footer {
  flex: none;
  padding-bottom: 114px;
}
@media only screen and (max-width: 1199px) {
  .footer {
    padding-bottom: 80px;
  }
}
@media only screen and (max-width: 767px) {
  .footer {
    padding-bottom: 40px;
  }
}